body {
  margin: 0 auto;
  height: 100vh;
  overflow: hidden;

}
#root{
  height: 100%;
  width: 100%;
}
.App {
  height: 100%;
  width: 100%;
  display: block;
  overflow: hidden;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.iconContent{
  display:grid;
  grid-template-columns: 1fr;
  grid-template-rows:auto auto;
  grid-template-areas:"iconImg" "iconTitle";
  text-align: center;
  max-width: fit-content;
}
.icon img{
  grid-area:iconImg;
  padding:10px;
  z-index:-1;
}
.icon p{
  grid-area:iconTitle;
  margin:0;
  padding:10px 0;
  z-index:-1;
}
.dragable{
  max-width: fit-content;
  border:5px solid red;
  position:absolute;
}
.blue{
  background:purple;
}
.orange{
  background:orange;
}
.drag-area{
  cursor: pointer;
}
.border{
  opacity:0.5;
}